/* Elements */

body, html{
    padding: 0;
    margin: 0;
    line-height: 1.6;
    font-family: 'Ubuntu', sans-serif;
}

/* Classes */

.menu{
    padding: 0;
    margin: 0;
    list-style: none;
    background-color: #1B3C59;
}

.menu a{
    text-align: center;
    transition-duration: 0.2s;
    display: block;
    padding: 20px 32px;
    color: white;
    text-decoration: none;
}
.menu a:hover{
    background-color: #275177;
}

.pagination{
  margin-top: 50px;
}

.pagination a{
  padding: 12px 0;
  margin: 5px;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  background-color: #494381;
}

.header{
  box-sizing: border-box;
  padding: 20px;
  height: auto;
  background-color: #b9ff80;

}

.content{
  // integrate the padding into the flex item's width
  box-sizing: border-box;
  padding: 20px;
  background-color: #8080ff;
}

.sidebar{
  box-sizing: border-box;
  padding: 40px;
  background-color: #f59e89;
}

.secondary{
  background-color: #ddc34e;
}

.footer{
  box-sizing: border-box;
  padding: 20px;
  background-color: #3168ff;
}

/* grid */

.item{
  color: rgb(56, 57, 58);
  background-color: aquamarine;
  border: 5px solid #275177;
  border-radius: 8px;
  padding: 20px 10px;
  font-size: 1.5em;
  text-align: center;
}